<template>
  <div class="resume-card-item">
    <img class="resume-card-item__logo" :src="item.logo" />
    <div class="resume-card-item__content">
        <div class="name" v-if="item.name">{{item.name}}</div>
        <div class="time-job" v-if="timeInfo">{{timeInfo}}</div>
        <div class="detail" v-if="item.detail">{{item.detail}}</div>
    </div>

    <div class="resume-card-item__link" @click.stop="handleLink">
      <Icon name="arrow"/>
    </div>  
  </div> 
</template>


<script>
import { Icon } from "vant";
export default {
  name: "resume-card-item",
  props: {
    item: {
      type: Object,
      required: true
    }
  },
  computed: {
    timeInfo() {
      const item = this.item;
      const time = `${item.startTime}~${item.endTime}`;
      const info = item.info;
      return [time, info].join();
    }
  },
  methods: {
    handleLink() {
      console.log("handlt to link", this.item);
    }
  },
  components: {
    Icon
  }
};
</script>

<style lang="postcss" scoped>
.resume-card-item {
  display: flex;
  &__logo {
    width: 50px;
    height: 50px;
    display: block;
    border-radius: 3px;
  }
  &__content {
    flex: 1;
    margin-left: 10px;
    margin-right: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    overflow: hidden;
    .name {
      font-size: 17px;
      font-weight: bold;
    }
    .time-job {
      font-size: 12px;
      color: #555;
    }
    .detail {
      font-size: 14px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
  &__link {
    width: 40px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
</style>



